import {GetTodayNew} from "../../common/api.js"
export default {
	data() {
		return {
			newList:[]
		}
	},
	onLoad() {
		this.pageInit();
	},
	methods: {
		pageInit(){
			GetTodayNew().then(res=>{
				if(res.status){
					this.newList = [...res.data];
				}
			})
		}
	}
}
